@use 'base/variables' as v;
@use 'base/mixins' as m;

.barra-mobile{
    background: rgba(14, 8, 8, 0.562);
    background: linear-gradient(351deg, rgba(14, 8, 8, 0.558) 9%, rgba(14, 10, 22, 0.363) 93%);
    padding: 2rem 4rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include m.tablet {
        display: none;
    }

    h1{
        font-family: 'Dancing Script', cursive;

        color: v.$blanco;
        margin: 0;
    }


    .menu{
        img{
            width: .8rem;
            &:hover{
                cursor: pointer;
            }
        }
    }
}



.barra{
    display: none;


    @include m.tablet {
        background-color: v.$blanco;
        padding: 1rem 2rem;
        box-shadow: rgba(0, 0, 0, 0.09) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;
    
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    p{
        margin: 0;
        font-size: 1.8rem;

        span{
            font-weight: 700;
            color: v.$indigo;
        }
    }

    .cerrar-sesion{
        @include m.boton( darken(v.$indigo, 20%), v.$blanco);
        font-size: 1.8rem;
        padding: 1rem;
        border-radius: 1.5rem;
    }
}