@use 'base/variables' as v;
@use 'base/mixins' as m;


.no-proyectos{

    a{
        color: v.$indigo;
    }
}
.listado-proyectos{

    list-style: none;
    padding: 0;
    margin-top: 3rem;


    @include m.grid(1, 2rem);

    @include m.tablet {
        @include m.grid(2, 2rem);
    }

    @include m.desktop {

        @include m.grid(3, 2rem)

    }


    .proyecto{
        display: flex;

        box-shadow: rgba(240, 46, 170, 0.4) -5px 5px, rgba(150, 46, 112, 0.411) -10px 10px, rgba(240, 46, 170, 0.2) -15px 15px, rgba(240, 46, 170, 0.1) -20px 20px, rgba(240, 46, 170, 0.05) -25px 25px;
        background-color: #4137afd3;
        border-radius: .5rem;
        min-height: 12rem;
        padding: 2rem;
        line-height: 2;
        align-items: center;
        color: v.$blanco;
        border-radius: .5rem;
        font-weight: 500;
        font-size:3.3rem;
        text-transform: uppercase;
        justify-content: center;
        &:hover{
            background-color: darken(#4137afd3, 10%);
        }

        a{
            color: v.$blanco;

        }

    }

    #boton{
        background-color: v.$rojo;
        color: v.$blanco;
        border: none;
        margin-top: 1rem;
        padding: 1rem;
        font-size: 1.3rem;
        border-radius: 1rem;
        text-align: center;        

        &:hover{
            cursor: pointer;
        }
}
}