.sidebar {
  opacity: 0;
  height: 0;
  overflow: hidden;
  transition-property: opacity;
  transition-duration: 500ms;
}
.sidebar.mostrar {
  padding: 2rem;
  opacity: 1;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(12, 10, 10, 0.932);
}
@media (min-width: 768px) {
  .sidebar.mostrar {
    display: none;
  }
}
.sidebar.ocultar {
  opacity: 0;
}
@media (min-width: 768px) {
  .sidebar {
    height: 100%;
    opacity: 1;
    padding: 2rem;
    background: rgb(255, 255, 255);
    background: linear-gradient(351deg, rgba(255, 255, 255, 0.5578606443) 9%, rgba(65, 55, 175, 0.8274509804) 93%);
  }
}
.sidebar .contenedor-sidebar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sidebar .contenedor-sidebar h2 {
  margin: 0;
  text-align: center;
  font-family: "Dancing Script", cursive;
  color: #FFFFFF;
  font-size: 5rem;
}
.sidebar .contenedor-sidebar .cerrar-menu {
  display: flex;
  justify-content: center;
  height: 2rem;
  width: 2rem;
}
.sidebar .contenedor-sidebar .cerrar-menu img:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar .contenedor-sidebar .cerrar-menu {
    display: none;
  }
}
.sidebar .sidebar-nav {
  margin-top: 5rem;
}
.sidebar .sidebar-nav a {
  display: block;
  padding: 0.7rem;
  transition-property: background-color;
  transition-duration: 300ms;
  text-transform: uppercase;
  color: #FFFFFF;
  font-weight: bold;
  font-size: 2.5rem;
  margin-bottom: 2rem;
  text-align: center;
}
.sidebar .sidebar-nav a:hover, .sidebar .sidebar-nav a.activo {
  background-color: #010102;
  border-radius: 0.5rem;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .sidebar .sidebar-nav a {
    font-size: 1.9rem;
    margin-bottom: 1.5rem;
    text-align: left;
    color: #000;
  }
}
