@use 'base/mixins' as m;
@use 'base/variables' as v;


@media (prefers-color-scheme: dark) {
    $grisOscuro: #2c2c3b;

    body {
        overflow: hidden;
        background-color: $grisOscuro;
    }

    .tagline,
    .formulario label,
    .login .acciones a,
    .crear .acciones a,
    .olvide .acciones a,
    .nombre-pagina {
        color: v.$blanco;
    }

    .descripcion-pagina,
    .formulario .campo input {
        color: darken(v.$grisClaro, 10%);
    }

    .formulario .campo input {
        background-color: lighten($grisOscuro, 15%);
        color: v.$blanco;
        &::placeholder {
            color: darken(v.$blanco, 30%);
        }
    }

    .alerta.error {
        background-color: v.$blanco;
    }

    .sidebar {
        .sidebar-nav a {
            color: v.$blanco;

            &.activo,
            &:hover {
                background-color: v.$naranja
            }
        }

    }

    .barra-mobile,
    .barra {
        background-color: darken($grisOscuro, 7%);
    }

    .barra {


        p {
            color: v.$blanco;

            span {
                color: v.$naranja;

            }
        }
        .cerrar-sesion{
            @include m.boton(v.$naranja, v.$blanco);
            padding: 1rem;

        }
    }


    .filtros {
        background-color: lighten($grisOscuro, 10%);

        h2,
        label {
            color: v.$blanco
        }

        input[type="radio"] {
            accent-color: v.$naranja;
        }
    }

    .dashboard {
        background-color: $grisOscuro;

        .enlace {
            color: v.$grisClaro;

            &:hover {
                color: darken(v.$grisClaro, 20%);
            }
        }

        .formulario input:not([type=submit]){
            background-color: lighten($grisOscuro, 20%);
            color: v.$blanco;
            
            &::placeholder{
                color: darken(v.$blanco, 30%);
            }
        }
    }

    .listado-proyectos .proyecto {

        &:hover {
            background-color: lighten($grisOscuro, 15%);

        }
    }

    .listado-tareas .tarea {
        background-color: lighten($grisOscuro, 10%);
        border: none;

        p {
            color: v.$blanco;
        }
    }

    .modal .formulario{
        background-color: $grisOscuro;
        input[type="text"]{
            background-color: lighten($grisOscuro, 20%);
            border: none;
            color: v.$blanco;
            
            &::placeholder{
                color: darken(v.$blanco, 20%);
            }
        }
        legend{
            color: v.$blanco;
        }


    }

    .no-tareas{
        color: v.$blanco; 
    }
}