@use 'base/variables' as v;
@use 'base/mixins' as m;

.sidebar {
    opacity: 0;
    height: 0;
    overflow: hidden;
    transition-property: opacity;
    transition-duration: 500ms;

    &.mostrar {
        padding: 2rem;
        opacity: 1;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(12, 10, 10, 0.932);

        @include m.tablet {
            display: none;
        }
    }

    &.ocultar{
        opacity: 0;
    }

    @include m.tablet {
        height: 100%;
        opacity: 1;
        padding: 2rem;
        background: rgb(255, 255, 255);
        background: linear-gradient(351deg, rgba(255, 255, 255, 0.5578606442577031) 9%, #4137afd3 93%);

    }

    .contenedor-sidebar{
        display: flex;
        justify-content: space-between;
        align-items: center;
        h2 {
            margin: 0;
            text-align: center;
            font-family: 'Dancing Script', cursive;
            color: v.$blanco;
            font-size: 5rem;
        }
        .cerrar-menu{
            display: flex;
            justify-content: center;
            height: 2rem;
            width: 2rem;
            img {
                &:hover{
                    cursor: pointer;
                }
            }

            @include m.tablet {
                display: none;
            }
        }
    }



    .sidebar-nav {
        margin-top: 5rem;

        a {
            display: block;
            padding: .7rem;
            transition-property: background-color;
            transition-duration: 300ms;
            text-transform: uppercase;
            color: v.$blanco;
            font-weight: bold;
            font-size: 2.5rem;
            margin-bottom: 2rem;
            text-align: center;
            &:hover,
            &.activo {
                background-color: darken(v.$indigo, 50%);
                border-radius: .5rem;
                color: v.$blanco;
            }

            @include m.tablet {
                font-size: 1.9rem;
                margin-bottom: 1.5rem;
                text-align: left;

                color: v.$negro;

            }
        }
    }

    .cerrar-sesion-mobile{
        margin-top: 10rem;
        .cerrar-sesion{
            @include m.boton(v.$indigo, v.$blanco);
        }


        @include m.tablet {
            display: none;
        }
    }

}