@use 'base/variables' as v;
@use 'base/mixins' as m;

.crear{
    margin-top: 5rem;
    .plustask {
        @include m.textoDegradado(v.$morado);

        @include m.tablet {
            font-size: 10rem;
        }
    }
    input[type="submit"]{
        @include m.boton(v.$morado, v.$blanco);
    }

    .acciones{
        a{
            color: v.$morado;
        }
    }
}