@use 'base/variables' as v;
@use 'base/mixins' as m;

.reestablecer{
    margin-top: 5rem;
    .plustask {
        @include m.textoDegradado(v.$azul);

        @include m.tablet {
            font-size: 10rem;
        }
    }
    input[type="submit"]{
        @include m.boton(v.$azul, v.$blanco);
    }

    .acciones{
        a{
            color: v.$azul;
        }
    }
}