@use 'base/variables' as v;
@use 'base/mixins' as m;

.confirmar {
    margin-top: 5rem;

    .plustask {
        @include m.textoDegradado(v.$indigo);

    }

    .acciones {
        justify-content: center;
        margin-bottom: 2rem;

        a {
            display: inline-block;
            width: 69%;
            background-color: rgb(0, 0, 0);
            border: none;
            padding: 10px 20px;
            text-decoration: none;
            color: rgb(255, 255, 255);
            cursor: pointer;
            border-radius: 5px;
            border-top: 2rem;
        }

        a:hover {

            transform: scale(1.1);
        }

    }
}