@use 'base/variables' as v;
@use 'base/mixins' as m;

.dashboard{
    background-color: rgb(243,243,243);

    @include m.tablet {
        display: flex;
        height: 100vh;
    }

    .principal{
        flex:1;
        overflow: scroll;
    }

    .contenido{
        padding: 2rem;
        .nombre-pagina{
            font-size: 4.8rem;
            text-align: center;
            text-transform: uppercase;
            line-height: 2;
            letter-spacing: 10px;  
        }
    }

    .formulario{

        input:not([type="submit"]){
            background-color: v.$blanco;
        }
        input[type="submit"]{
            @include m.boton(v.$indigo, v.$blanco);
            padding: 1rem;
            text-transform: uppercase;   
            font-weight: bold;
            }
    }

    .enlace{
        color: v.$indigo;

        &:hover{
            color: darken(v.$indigo, 20%)
        }
    }
}